"use client";

import Image from "next/image";
import Link from "next/link";
import { useMemo, useState } from "react";
import { useParams } from "next/navigation";

import {
  subcategories,
  products,
  productVariants,
  productImages,
  subsubcategories, // ‚úÖ must exist in store.ts
} from "@/data/store";

/** ===== helpers ===== */
function money(n: number) {
  return `‚Çπ${Number(n ?? 0).toFixed(0)}`;
}

function getPrimaryImageUrl(productId: number) {
  const primary = productImages.find((img) => img.product_id === productId && img.is_primary);
  const any = productImages.find((img) => img.product_id === productId);
  return primary?.url || any?.url || "/example.png";
}

function getBestVariant(productId: number, basePrice?: number) {
  const vars = productVariants.filter((v) => v.product_id === productId);
  if (vars.length === 0) {
    return { price: basePrice ?? 0, mrp: null as number | null, label: "" };
  }
  const best = vars.reduce((min, v) => (v.price < min.price ? v : min), vars[0]);
  return { price: best.price, mrp: best.mrp ?? null, label: best.label ?? "" };
}

export default function SubcategoryPage() {
  const { slug } = useParams<{ slug: string }>();

  const currentSub = useMemo(
    () => subcategories.find((s) => s.slug === slug),
    [slug]
  );

  if (!currentSub) {
    // client page: simple fallback
    return (
      <main className="min-h-screen bg-white p-6 text-black">
        Subcategory not found.
      </main>
    );
  }

  // ‚úÖ Sub-subcategories under this subcategory
  const ssList = useMemo(
    () => (subsubcategories ?? []).filter((x: any) => x.subcategory_id === currentSub.id),
    [currentSub.id]
  );

  // selected sub-subcategory slug (null = All)
  const [activeSS, setActiveSS] = useState<string | null>(null);

  // Products in this subcategory
  const baseList = useMemo(
    () => products.filter((p: any) => p.subcategory_id === currentSub.id),
    [currentSub.id]
  );

  // ‚úÖ Filtered products (only show items from chosen sub-subcategory)
 const filtered = useMemo(() => {
  if (!activeSS) return baseList; // All
  return baseList.filter((p: any) => (p.tags ?? []).includes(activeSS));
}, [activeSS, baseList]);


  // Optional: title (JioMart shows "Search Results" often; you can keep subcategory name)
  const titleText = activeSS
    ? ssList.find((x: any) => x.slug === activeSS)?.name ?? currentSub.name
    : currentSub.name;

  return (
    <main className="min-h-screen bg-[#F4F6F8]">
      {/* ===== TOP BLUE HEADER ===== */}
      <header className="sticky top-0 z-50 bg-[#0B6EA9]">
        <div className="mx-auto max-w-md px-3 py-2 flex items-center gap-2">
          <Link href="/" className="h-10 w-10 grid place-items-center rounded-full bg-[#0A5F91] text-white">
            ‚Üê
          </Link>

          <div className="flex-1 h-10 rounded-full bg-[#0A5F91] px-4 flex items-center text-white/90 text-sm">
            üîé Search JioMart
          </div>

          <Link href="/cart" className="relative h-10 w-10 grid place-items-center rounded-full bg-[#0A5F91] text-white">
            üõí
            <span className="absolute -top-1 -right-1 bg-red-500 text-[10px] px-1.5 rounded-full">8</span>
          </Link>
        </div>

        {/* ===== LOCATION ROW ===== */}
        <div className="bg-white border-t">
          <div className="mx-auto max-w-md px-4 py-2 text-sm text-gray-800 flex items-center gap-2">
            üìç <span className="font-medium">Mumbai 400020</span> <span className="text-gray-500">‚ñæ</span>
          </div>
        </div>
      </header>

      {/* ===== TITLE + SORT/FILTER + CHIPS ===== */}
      <section className="bg-white border-b">
        <div className="mx-auto max-w-md px-4 py-3 flex items-center justify-between">
          <div className="text-sm font-semibold text-gray-900">{titleText}</div>

          <div className="flex gap-2">
            <button className="h-9 px-3 rounded-full border text-sm bg-white flex items-center gap-2">
              ‚Üï Sort
            </button>
            <button className="h-9 px-3 rounded-full border text-sm bg-white flex items-center gap-2">
              ‚öô Filter
            </button>
          </div>
        </div>

        <div className="mx-auto max-w-md px-4 pb-3 flex gap-2 overflow-x-auto">
          <button className="h-8 px-3 rounded-full bg-gray-100 text-xs whitespace-nowrap flex items-center gap-2">
            ‚Üï By Popularity ‚ñæ
          </button>
          <button className="h-8 px-3 rounded-full bg-gray-100 text-xs whitespace-nowrap">Brands ‚ñæ</button>
          <button className="h-8 px-3 rounded-full bg-gray-100 text-xs whitespace-nowrap">Filters ‚ñæ</button>
        </div>
      </section>

      {/* ===== MAIN: LEFT RAIL + GRID ===== */}
      <section className="mx-auto max-w-md grid grid-cols-[96px_1fr]">
        {/* LEFT RAIL (All + sub-subcategories only) */}
        <aside className="bg-white border-r">
          {/* ALL */}
          <button
            onClick={() => setActiveSS(null)}
            className={`w-full block px-2 py-3 border-l-4 ${
              activeSS === null ? "border-[#0B6EA9] bg-[#EAF4FB]" : "border-transparent"
            }`}
          >
            <div className="w-12 h-12 mx-auto rounded-xl bg-gray-100 overflow-hidden grid place-items-center">
              <span className="text-[11px] font-extrabold text-gray-700">All</span>
            </div>
            <div className={`mt-2 text-[11px] text-center ${activeSS === null ? "text-[#0B6EA9] font-semibold" : "text-gray-700"}`}>
              All
            </div>
          </button>

          {/* SUB-SUBCATEGORIES */}
          {ssList.map((ss: any) => {
            const isActive = activeSS === ss.slug;
            return (
              <button
                key={ss.id}
                onClick={() => setActiveSS(ss.slug)}
                className={`w-full block px-2 py-3 border-l-4 ${
                  isActive ? "border-[#0B6EA9] bg-[#EAF4FB]" : "border-transparent"
                }`}
              >
                <div className="w-12 h-12 mx-auto rounded-xl bg-gray-100 overflow-hidden grid place-items-center">
                  {/* Replace this with an icon/image if you want */}
                  <span className="text-[10px] font-bold text-gray-700 text-center px-1 leading-tight">
                    {ss.name?.split(" ").slice(0, 2).join(" ")}
                  </span>
                </div>
                <div className={`mt-2 text-[11px] leading-tight text-center ${isActive ? "text-[#0B6EA9] font-semibold" : "text-gray-700"}`}>
                  {ss.name}
                </div>
              </button>
            );
          })}
        </aside>

        {/* RIGHT GRID */}
        <div className="p-3">
          <div className="grid grid-cols-2 gap-3">
            {filtered.map((p: any) => {
              const imgUrl = getPrimaryImageUrl(p.id);
              const { price, mrp, label } = getBestVariant(p.id, p.base_price);
              const offPct = mrp && price ? Math.round(((mrp - price) / mrp) * 100) : null;

              return (
                <div key={p.id} className="bg-white rounded-2xl shadow-sm border overflow-hidden">
                  <div className="relative p-2">
                    <button className="absolute right-2 top-2 w-8 h-8 rounded-full bg-white shadow grid place-items-center text-gray-500">
                      ‚ô°
                    </button>

                    {label ? (
                      <div className="absolute right-2 bottom-2 text-[10px] px-2 py-0.5 rounded-full bg-black/70 text-white">
                        {label}
                      </div>
                    ) : null}

                    <Link href={`/product/${p.slug}`} className="block">
                      <Image
                        src={imgUrl}
                        alt={p.name}
                        width={220}
                        height={220}
                        className="mx-auto h-28 w-auto object-contain"
                      />
                    </Link>
                  </div>

                  <div className="px-3 pb-3">
                    <div className="text-[12px] text-gray-900 line-clamp-2 min-h-[34px]">{p.name}</div>

                    <div className="mt-1 flex items-end gap-2">
                      <div className="text-[14px] font-extrabold text-gray-900">{money(price)}</div>
                      {mrp ? <div className="text-[11px] text-gray-400 line-through">{money(mrp)}</div> : null}
                    </div>

                    {offPct ? (
                      <div className="text-[12px] font-semibold text-red-600">{offPct}% Off</div>
                    ) : (
                      <div className="h-[18px]" />
                    )}

                    <button className="mt-2 w-full h-10 rounded-xl border-2 border-[#0B6EA9] text-[#0B6EA9] font-bold flex items-center justify-center gap-2">
                      Add <span className="text-xl leading-none">+</span>
                    </button>

                    <div className="mt-2 h-7 rounded-lg bg-[#E8F5EE] text-[#0F8A4B] text-xs font-semibold grid place-items-center">
                      ‚ö° Quick
                    </div>
                  </div>
                </div>
              );
            })}

            {filtered.length === 0 && (
              <div className="col-span-2 bg-white rounded-2xl border p-4 text-sm text-gray-600">
                No products found in this section.
                <div className="mt-1 text-xs text-gray-500">
                  (Make sure products have <code>subsub_slug</code> or <code>subsub_id</code>.)
                </div>
              </div>
            )}
          </div>
        </div>
      </section>

      {/* ===== BOTTOM NAV ===== */}
      <nav className="sticky bottom-0 bg-white border-t">
        <div className="mx-auto max-w-md grid grid-cols-4 py-2 text-xs text-gray-700">
          <Link href="/" className="grid place-items-center gap-1">
            üè†<span>Home</span>
          </Link>
          <Link href="/categories" className="grid place-items-center gap-1">
            üìÇ<span>Categories</span>
          </Link>
          <Link href="/wishlist" className="grid place-items-center gap-1">
            ‚ô°<span>Wishlist</span>
          </Link>
          <Link href="/orders" className="grid place-items-center gap-1">
            üì¶<span>Orders</span>
          </Link>
        </div>
      </nav>
    </main>
  );
}
